.post {
  grid-row: 1 / 2;
  display: grid;
  grid-template-columns: 20% 80%;
  img {
    width: 60%; } }

.postAvatar {
  grid-column: 1/2;
  text-align: center; }

.postText {
  grid-column: 2/3;
  align-self: center;
  font-size: 1.8vw; }

.like {
  grid-column: 2 / 3;
  width: 10%;
  height: 1.5vw;
  font-size: 0.9vw;
  margin-left: 40%;
  background: cornflowerblue;
  border: none;
  cursor: pointer;
  span {
    color: white; } }
